import React from 'react'
import Result from "./result";
import { useState} from "react";

export default function Buttons() {
    const [result, setResult] = useState("");
    const digit = "0123456789()";
    const item = "0123456789/*-+)";
    document.addEventListener('keypress',(e)=>{
        let name = e.key;
        if(item.includes(name)){
            setResult(result+name)
        }
        else if (name==="(") {
            if (result==="") {
                setResult(result+'(') 
            }
            else if (digit.includes(result.slice(-1))) {
                setResult(result+'*(') 
            }else{
                setResult(result+'(') 
                }
        }
    });

    return (
        <div>
            <Result res = {result}/>
            <div className="button"> 
                <button name="(" className="op" onClick={()=>{
                    if (result==="") {
                        setResult(result+'(') 
                    }
                    else if (digit.includes(result.slice(-1))) {
                        setResult(result+`*(`) 
                    }else{
                        setResult(result+'(') 
                        }
                    }
                    }>(</button>
                <button name=")" className="op" onClick={()=>{setResult(result+')')}}>)</button>
                <button name="CE" className="clc" onClick={()=>{let r = result.slice(0, -1);setResult(r)}}>CE</button>
                <button name="C" className="clc" onClick={()=>{setResult("")}}>C</button><br/>


                <button name="1" onClick={()=>{setResult(result+'1')}}>1</button>
                <button name="2" onClick={()=>{setResult(result+'2')}}>2</button>
                <button name="3" onClick={()=>{setResult(result+'3')}}>3</button>
                <button name="+" className="op" onClick={()=>{setResult(result+'+')}}>+</button><br/>


                <button name="4" onClick={()=>{setResult(result+'4')}}>4</button>
                <button name="5" onClick={()=>{setResult(result+'5')}}>5</button>
                <button name="6" onClick={()=>{setResult(result+'6')}}>6</button>
                <button name="-" className="op" onClick={()=>{setResult(result+'-')}}>-</button><br/>

                <button name="7" onClick={()=>{setResult(result+'7')}}>7</button>
                <button name="8" onClick={()=>{setResult(result+'8')}}>8</button>
                <button name="9" onClick={()=>{setResult(result+'9')}}>9</button>
                <button name="*" className="op" onClick={()=>{setResult(result+'*')}}>x</button><br/>


                <button name="." onClick={()=>{setResult(result+'.')}}>.</button>
                <button name="0" onClick={()=>{setResult(result+'0')}}>0</button>
                <button name="=" id="equal" onClick={()=>{
                    try {
                        setResult(eval(result).toString()); 
                    } catch (e) {
                        if (e instanceof SyntaxError) {
                            alert(e.message);
                        }
                    } 
                }}>=</button>
                <button name="/" className="op" onClick={()=>{setResult(result+'/')}}>รท</button><br/>
                </div>
        </div>
    )
}
